import { createWebHistory, createRouter } from "vue-router";
import Product from './components/ProductWrap.vue'
import Service from './components/ServiceWrap.vue'
import Game from './components/GameWrap.vue'

const routes = [
    {
        path: '/', 
        component: Product 
    },
    {
        path: '/service', 
        component: Service
    },
    {
        path: '/game', 
        component: Game
    }
]


const router = createRouter({
    history: createWebHistory(), routes,
   });
   export default router; 