<template lang="">
    <div id="container">
        <h2>word relay</h2>
        <div class="in_wrap">
            <div class="content">
                <h3>{{word}}</h3>
                <form v-on:submit.prevent="onSubmit">
                    <input  type="text" v-model="value">
                    <button type="submit"  class="more">입력</button>
                </form>
                <div id="result">
                    {{result}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>

export default {
            data(){
                return {
                word:'장미',
                value:'',
                result:''
                }
            },
            methods: {
                onSubmit(){
                    console.log(this.word[this.word.length-1])
                    if(this.value[0]===this.word[this.word.length-1]){
                        this.result='정답'
                        this.word=this.value
                        this.value=''
                    }else{
                        this.result='땡'
                        this.value=''
                    }
                }
            }


    }

</script>

<style scoped>
#container {
 margin-bottom: 10rem
 }
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: 215px;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
 .more {
    border: none;
    color: rgb(77, 77, 77);
    border-radius: 2rem;
    padding: 0 1rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 3rem;
    cursor: pointer;
  }

</style>