<template lang="">
    <div id="container">
        <h2>flower list</h2>
        <div class="in_wrap">
            <div class="content" v-for="(f,i) in flowers" :key="i">
                <h3 @click="modalOpen(i)">{{f.title}}</h3>
                <img :src="f.image" :alt="f.price" @click="modalOpen(i)">
                <Modal :modal="modal" :flower="flowers" :crt="crt"
                @closeModal="modalClose"></Modal>
            </div>
        </div>
        <button class="more" @click="more">더보기</button>
    </div>
</template>
<script>
import flower from '../assets/flower.js'
import Modal from './ModalWrap'
import axios from 'axios';
export default {
    data(){
        return {
            flowers:flower,
            modal:false,
            crt:0,
            moreCrt:0
        }
    },
    methods: {
        modalOpen(i){
            this.crt=i
            this.modal=!this.modal;
        },
        modalClose(){
            this.modal=false
        },
        more(){

            axios.get(`https://zhelddlek.github.io/data/more${this.moreCrt}.json`).then( result => {
                this.flowers.push(result.data);
            })
            this.moreCrt++
            if(this.moreCrt==2){
                this.moreCrt=0
            }
        }
    },
    components:{
        Modal
    }
}
</script>
<style scoped>
#container {
 margin-bottom: 10rem
 }
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: 215px;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
 .more {
    border: none;
    color: rgb(77, 77, 77);
    border-radius: 2rem;
    padding: 0 1rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 3rem;
    cursor: pointer;
  }
</style>